import {Box, Button, Container, Tab, Tabs } from "@mui/material";
import { useState } from "react";


const Header = () => {
    const [tabValue, setTabValue] = useState<string>("Home")

    const launchApp = () => {
        window.open("https://www.moveflow.xyz/");
    }

    const onTabChange = (event: React.SyntheticEvent, newValue: string) => {
        console.log('newValue', newValue);
        setTabValue(newValue);
        let anchorElement = document.getElementById(newValue);
        if(anchorElement) { anchorElement.scrollIntoView({block: "start", inline: "nearest"}); }
    }

    return (
        <Container sx={{marginTop: 1}}>
            <div className="flex flex-row items-center justify-between">
                <Box>
                    <span className="text-gradient-primary" style={{
                        fontWeight: '900',
                        fontSize: 'x-large'
                    }}>
                      MoveFlow
                    </span>
                </Box>

                <div className="flex flex-row items-center justify-end gap-x-5">
                    <Tabs
                      value={tabValue}
                      onChange={onTabChange}
                      textColor="inherit"
                    >
                        <Tab label="Home" value="Home"/>
                        <Tab label="Product" value="Product"/>
                        <Tab label="Partner" value="Partner"/>
                        <Tab label="About" value="About"/>
                    </Tabs>
                    <Button variant="contained" onClick={launchApp}>Launch App</Button>
                </div>
            </div>
        </Container>
        
    )
}

export default Header;