import {Card, CardContent, CardMedia, Typography} from "@mui/material";
import React, { ReactNode } from "react";

export interface MyCardProps {
  img: string
  title: string
  content: ReactNode
  componentType: "img" | "svg"
  height: number
}

const MyCard = (props: MyCardProps) => {
  const {img, title, content, componentType, height} = props;
  return (
    <Card sx={{width: "100%", background: "transparent"}}>
      <CardMedia
        component={componentType}
        image={img}
        alt={`${title}-avatar`}
        sx={{ height: height, objectFit: "initial"  }}
      />
      <CardContent>
        <Typography gutterBottom variant="h6" component="div" sx={{textAlign: "left"}}>
          {title}
        </Typography>
        <Typography variant="body2" component="div"  sx={{textAlign: "left"}}>
          {content}
        </Typography>
      </CardContent>
    </Card>
  )
}

export default MyCard;